import React,{useContext,useState} from 'react'
import  {GlobalTransaction} from "./GlobalContext"
import TransactionList from "./TransactionList"
const Transaction = () => {
  const {transactions} = useContext(GlobalTransaction)
  
    return (
        <div className="container p-0">
          <table className="table table-hover table-striped table-bordered">
  <thead className="thead text-white">
     
      <th scope="col">Description</th>
      <th scope="col">Amount</th>
      <th scope="col">DeleteItem</th>
  </thead>
    <tbody>
    {transactions.map((trans,index)=>{
              return <TransactionList props={trans} id={index} key={trans.id}></TransactionList>
            })}
  
    </tbody>
            
          
          </table>  
        </div>
    )
}

export default Transaction
