import React,{useContext} from 'react'
import  {GlobalTransaction} from "./GlobalContext"
import AttachMoneyIcon from '@material-ui/icons/AttachMoney';
const Header = () => {
    const {transactions}=useContext(GlobalTransaction)
    const array=transactions.map((values)=>values.amount)
    const amount=array.reduce((amt,item)=>(amt+=item),0).toFixed(2)
    return (
        <>
            <h4>$ {amount}</h4>
            <h6>My Balance</h6>
        </>
    )
}

export default Header
