import { Mail, MailOutline } from '@material-ui/icons'
import React ,{useContext}from 'react'
import  {GlobalTransaction} from "./GlobalContext"
import DeleteIcon from '@material-ui/icons/Delete';
const TransactionList = ({props,id}) => {
    const sign=props.amount>0 ?  "text-success":"text-danger"
    const {deleteTransaction}=useContext(GlobalTransaction)

    return (
      <tr>
      
      <td scope="row">{props.text}</td>
      <td className={sign} style={{fontWeight:"bold"}}>{props.amount}</td>
      <td><button className="btn btn-danger" onClick={()=>deleteTransaction(id)}>&times;</button></td>
    </tr>
    )
}

export default TransactionList
