import React from "react"
import "../node_modules/bootstrap/dist/css/bootstrap.min.css"
import  "./App.css"
import AddTransaction from "./Components/AddTransaction"
import Expenses from "./Components/Expenses"
import GlobalContext from "./Components/GlobalContext"
import Header from "./Components/Header"
import Transaction from "./Components/Transaction"
import Footer from "./Footer"
const App = () => {
    return (
        <>
        <GlobalContext >
            <section className="pagecontainer min-vh-100">
            <h2 className="text-center mt-3">Expense Tracker</h2>
            <div className="container  mt-3 maincontainer">
                
                <div className="row">
                    <div className="col-md-6">
                        <div className="container-fluid maincontainer px-0">
                            <div className="container-fluid  headerT py-3 ">
                                <img src="https://picsum.photos/id/237/100/100" className="img-fluid"  style={{borderRadius:"50%",border:"2px solid white"}}></img>
                                <label className="label">Michael Jackson</label>
                                
                            </div>
                            <hr></hr>
                          <div className="p-3">
                              <Header/>
                              <hr></hr>
                              <Expenses/>
                          </div>
                          
                        </div>
                    
            
            <AddTransaction/>
                    </div>
                    <div className="col-md-6 overflow-auto table mt-3 mt-lg-0">
                    <Transaction/>
                    </div>
                </div>
        
        </div>
        
        </section>
        </GlobalContext>
        <Footer></Footer>
        </>
    )
}

export default App
