export default (state,action) =>{
    switch(action.type){
        case "delete":
            return {
                ...state,
                transactions:state.transactions.filter((tran,index)=>index != action.payload)
            }
        case "add":

        return {
            ...state,
            transactions:[action.payload,...state.transactions]
        }
        default:
            return state;
    }
};