import React,{useContext,useState} from 'react'
import  {GlobalTransaction} from "./GlobalContext"
const Expenses = () => {
    const {transactions}=useContext(GlobalTransaction)
    const array=transactions.map(trans=>trans.amount)
    console.log(array)
    const pos=array.filter((val)=>val>0).reduce((amt,item)=>(amt+=item),0).toFixed(2)
    const neg=array.filter((val)=>val<0).reduce((amt,item)=>(amt+=item),0).toFixed(2)
    console.log(pos,neg)
    
    

    return (
        <div className="container mt-2">
            <div className="row">
                <div className="col-md-6 right">
                    <p><strong>INCOME</strong></p>
                    <p>{pos}</p>
                </div>
                <div className="col-md-6 mt-2 mt-lg-0 left">
                    <p><strong>EXPENSES</strong></p>
                    <p>{Math.abs(neg)}</p>
                </div>
            </div>
            
        </div>
    )
}

export default Expenses
