import React ,{useState,useContext} from  'react'
import Swal from 'sweetalert2'

import  {GlobalTransaction} from "./GlobalContext"
const AddTransaction = () => {
  const [text, settext] = useState("")
  const [amount, setamount] = useState("")
  const [buttonChange, setbuttonChange] = useState("")
  const {transactions,addTransaction,addUpdateTransaction}=useContext(GlobalTransaction)
 
const adding=(e)=>{
e.preventDefault()


const amounts= buttonChange=="pos"? +amount:Math.abs(+amount)*-1
const msg=buttonChange=="pos"? "Cash Added Successfully!":"Cash Debited Successfully!"
const newTransactin={
  
  "text":text,
  "amount":amounts
}
if (text==""){
  Swal.fire({
    icon: 'error',
    title: 'Oops...',
    text: 'Please Enter Description!',
    
  })
  return false
}
else if (amounts==0) {
  Swal.fire({
    icon: 'error',
    title: 'Oops...',
    text: "You didn't Enter Amount!",
    
  })
  return false
}
else if (amount<0) {
  Swal.fire({
    icon: 'error',
    title: 'Oops...',
    text: "Invalid Amount",
    
  })
  return false
}
console.log(newTransactin)
addTransaction(newTransactin,msg)
settext("")
setamount("")
  }
    return (
        <div className="container my-3">
            <form onSubmit={adding}>
  <div className="form-group">
    
    <input type="text" className="form-control shadow-none" onChange={(e)=>settext(e.target.value)} aria-describedby="emailHelp" value={text} placeholder="Description"/>
    
  </div>
  <div className="form-group">
    
    <input type="number" className="form-control shadow-none" onChange={(e)=>setamount(e.target.value)} id="exampleInputPassword1" value={amount} placeholder="Enter Amount"/>
  </div>
  <div className="container-fluid p-0">
    <div className="row">
      <div className="col-6">
      <button type="submit" className="btn btn-primary btn-block border-0" style={{backgroundColor:"#4568dc"}} onClick={()=>setbuttonChange("pos")} >Add Cash</button>
      </div>
      <div className="col-6">
      <button type="submit" className="btn btn-primary btn-block border-0" style={{backgroundColor:"#b06ab3"}} onClick={()=>setbuttonChange("neg")} >Deduct Cash</button>
      </div>

    </div>
  </div>
  
</form>

        </div>
    )
}

export default AddTransaction
