import React,{createContext,useReducer,useState} from 'react'
import Swal from 'sweetalert2'
import AppReducer from "./AppReducer"
import IdReducer from "./IdReducer"
const initialState={
    transactions:[{"text":"salary","amount":200},{"text":"pention","amount":200},{"text":"grossery","amount":-200}]
}
const GlobalTransaction=createContext()
const GlobalContext = ({children}) => {
    
    const [state,dispatch]=useReducer(AppReducer,initialState)
    const deleteTransaction=(id)=>{
        Swal.fire({
            title: 'Are you sure?',
            text: "You won't be able to revert this!",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Yes, delete it!'
          }).then((result) => {
            if (result.isConfirmed) {
              Swal.fire({
                  icon:"success",
                  title:"Deleted!",
                  text:'Transaction Deleted!',
                  showConfirmButton: false,
                  timer:1500
              }
                
                
              )
              dispatch({
                type:"delete",
                payload:id
            })
            }
          })
        

       
    }
    const addTransaction=(tran,msg)=>{
        dispatch({
            type:"add",
            payload:tran
        })
        Swal.fire({
            icon: 'success',
            showConfirmButton: false,
            text: msg,
            timer:1500
          })

    }
    const addUpdateTransaction=(tran)=>{
        dispatch({
            type:"addupdate",
            payload:tran
        })

    }
    
    return (
        <GlobalTransaction.Provider value={{transactions:state.transactions,deleteTransaction,addTransaction,addUpdateTransaction}}>
          {children}
        </GlobalTransaction.Provider>
    )
}

export default GlobalContext
export {GlobalTransaction}
